














import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import DCascader from '../../d-cascader';
import {Area} from './area';
import {China} from './static/data';

Vue.use(DCascader);
@Component({
  name: 'DProvince',
  components: {}
})
export default class VProvince extends Vue {
  @Prop({
    type: String
  })
  public size: 'small' | 'large' | 'default';
  @Prop({
    type: Array
  }) // string[] | number[]
  public value: string[]; // string large default small
  public data: Area[] = China; // 
  public names: string[] = []; // 
  /**
   * 
   */
  public handleChanged(val: string[]): void {
    console.table(val);
    this.$emit('change', val);
  }
}
